html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  background-color: var(--main-background);
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

div, button {
  font-size: 32;
  font-weight: 700;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --numbers-color: hsl(221, 14%, 31%);
  --numbers-background: hsl(30, 25%, 89%);
  --numbers-shadow: hsl(28, 16%, 65%);
  --signs-color: hsl(221, 14%, 31%);
  --signs-background: hsl(30, 25%, 89%);
  --signs-shadow: hsl(28, 16%, 65%);
  --header-color: #fff;
  --screen-color: #fff;
  --del-color: #fff;
  --del-background: hsl(225, 21%, 49%);
  --del-shadow: hsl(224, 28%, 35%);
  --reset-color: #fff;
  --reset-background: hsl(225, 21%, 49%);
  --reset-shadow: hsl(224, 28%, 35%);
  --equal-color: #fff;
  --equal-background: hsl(6, 63%, 50%);
  --equal-shadow: hsl(6, 70%, 34%);
  --main-background: hsl(222, 26%, 31%);
  --key-pad: hsl(223, 31%, 20%);
  --screen-background: hsl(224, 36%, 15%);
  --key-background: hsl(225, 21%, 49%);
  --key-shadow: hsl(224, 28%, 35%);
  --red-key: hsl(6, 63%, 50%);
  --red-key-shadow: hsl(6, 70%, 34%);
  --grayish-key: hsl(30, 25%, 89%);
  --grayish-key-shadow: hsl(28, 16%, 65%);
  --vary-dark: hsl(221, 14%, 31%);
  --white: #fff;
  --toggler-background: hsl(223, 31%, 20%);
}

.theme-two {
  --numbers-color: hsl(60, 10%, 19%);
  --numbers-background: hsl(30, 25%, 89%);
  --numbers-shadow: hsl(28, 16%, 65%);
  --signs-color: hsl(60, 10%, 19%);
  --signs-background: hsl(30, 25%, 89%);
  --signs-shadow: hsl(28, 16%, 65%);
  --header-color: hsl(60, 10%, 19%);
  --screen-color: hsl(60, 10%, 19%);
  --del-color: #fff;
  --del-background: hsl(225, 21%, 49%);
  --del-shadow: hsl(224, 28%, 35%);
  --reset-color: #fff;
  --reset-background: hsl(225, 21%, 49%);
  --reset-shadow: hsl(224, 28%, 35%);
  --equal-color: #fff;
  --equal-background: hsl(6, 63%, 50%);
  --equal-shadow: hsl(6, 70%, 34%);
  --main-background: hsl(0, 0%, 90%);
  --key-pad: hsl(0, 5%, 81%);
  --screen-background: #fff;
  --key-background: hsl(225, 21%, 49%);
  --key-shadow: hsl(224, 28%, 35%);
  --red-key: hsl(6, 63%, 50%);
  --red-key-shadow: hsl(6, 70%, 34%);
  --grayish-key: hsl(30, 25%, 89%);
  --grayish-key-shadow: hsl(28, 16%, 65%);
  --vary-dark: hsl(221, 14%, 31%);
  --white: #fff
  --toggler-background: hsl(0, 5%, 81%);
}

.theme-three {
  --numbers-color: hsl(52, 100%, 62%);
  --numbers-background: hsl(268, 47%, 21%);
  --numbers-shadow: hsl(290, 70%, 36%);
  --signs-color: hsl(52, 100%, 62%);
  --signs-background: hsl(268, 47%, 21%);
  --signs-shadow: hsl(290, 70%, 36%);
  --header-color: hsl(52, 100%, 62%);
  --screen-color: hsl(52, 100%, 62%);
  --del-color: #fff;
  --del-background: hsl(281, 89%, 26%);
  --del-shadow: hsl(285, 91%, 52%);
  --reset-color: #fff;
  --reset-background: hsl(281, 89%, 26%);
  --reset-shadow: hsl(285, 91%, 52%);
  --equal-color: hsl(198, 20%, 13%);
  --equal-background: hsl(176, 100%, 44%);
  --equal-shadow: hsl(177, 92%, 70%);
  --main-background: hsl(268, 75%, 9%);
  --key-pad: hsl(268, 71%, 12%);
  --screen-background: hsl(268, 71%, 12%);
  --key-background: hsl(225, 21%, 49%);
  --key-shadow: hsl(224, 28%, 35%);
  --red-key: hsl(6, 63%, 50%);
  --red-key-shadow: hsl(6, 70%, 34%);
  --grayish-key: hsl(30, 25%, 89%);
  --grayish-key-shadow: hsl(28, 16%, 65%);
  --vary-dark: hsl(221, 14%, 31%);
  --toggler-background: hsl(268, 71%, 12%);
}

:root {
  --font-inter: "Spartan", sans-serif;
}

.container {
  width: 100%;
  height: 100%;
  padding: 20px;
  display: grid;
  grid-template-rows: 30px 120px 1fr;
  grid-gap: 10px;
}

.screen {
  width: 500px;
  height: 100px;
  border-radius: 5px;
  background-color: var(--screen-background);
  color: var(--screen-color);
  margin: auto;
}
@media (max-width: 39.9375em) {
  .screen {
    width: 300px;
  }
}

.buttons-pad {
  padding: 30px;
  width: 500px;
  border-radius: 5px;
  background-color: var(--key-pad);
  display: grid;
  justify-content: center;
  align-items: center;
  grid-gap: 25px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: auto;
  grid-template-areas: "a b c d" "f g h i" "j j l m" "n o p q" "button button button1 button1";
  margin: auto;
}
@media (max-width: 39.9375em) {
  .buttons-pad {
    width: 300px;
  }
}

.base-button {
  height: 50px;
  border-radius: 5px;
  border: none;
}

button:active {
  border: 2px solid;
}

.number {
  color: var(--numbers-color);
  background-color: var(--numbers-background);
  box-shadow: 0px 5px var(--numbers-shadow);
}

.sign {
  color: var(--signs-color);
  background-color: var(--signs-background);
  box-shadow: 0px 5px var(--signs-shadow);
}

.header {
  margin: auto;
  color: var(--header-color);
  justify-content: space-between;
  width: 500px;
  display: flex;
}
@media (max-width: 39.9375em) {
  .header {
    width: 300px;
  }
}
.header .title {
  align-self: center;
}

.screen {
  display: grid;
  color: var(--screen-color);
  align-items: center;
}

.input {
  color: inherit;
  width: 100%;
  text-align: right;
}

.theme-toggler-container {
  display: flex;
  flex-direction: column;
}
.theme-toggler-container .theme-toggler-title {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.theme-toggler {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  border-radius: 20px;
  background-color: var(--toggler-background);
}

.theme-check input {
  display: none;
}

.theme-check .checkmark {
  width: calc(100% - 6px);
  height: calc(100% - 6px);
  background-color: #d03f2f;
  border-radius: 50%;
  display: inline-block;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.theme-check input:checked + .checkmark {
  opacity: 1;
  display: inline-block;
}

.theme-check {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.del {
  color: var(--del-color);
  background-color: var(--del-background);
  box-shadow: 0px 5px var(--del-shadow);
}

.reset {
  color: var(--reset-color);
  background-color: var(--reset-background);
  box-shadow: 0px 5px var(--reset-shadow);
}

.equal {
  color: var(--equal-color);
  background-color: var(--equal-background);
  box-shadow: 0px 5px var(--equal-shadow);
}

.large {
  grid-column: span 2;
}

footer {
  margin-top: 20px;
  color: var(--del-color);
  padding: 5px;
}