html{
    font-size: 100%;
    box-sizing: border-box;
}
*, *::before,*::after{
    box-sizing: inherit;
}

body{
    background-color: var(--main-background);
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
}

div, button{
    font-size: 32;
    font-weight: 700;
}